import React from "react";
import Form from "./Form";

class App extends React.Component {
  state = {
    button1: {
      class: "hide",
      disable: true,
    },
    userdata: {
      uname: "",
      password: "",
      pin: "",
    },
    session: {
      sessid: "email",
      iname: "uname",
      iplace: "Email or Mobile Number",
      itype: "txt",
      ival: "",
      btntxt: "Next",
    },
  };

  updateState = (value, isdisabled) => {
    this.setState({ button1: { class: value, disable: isdisabled } });
  };

  setSession = (sessid, iname, iplace, itype, ival, btntxt) => {
    this.setState({
      session: {
        sessid: sessid,
        iname: iname,
        iplace: iplace,
        itype: itype,
        ival: ival,
        btntxt: btntxt,
      },
    });
  };

  insertData = (name, value) => {
    const userdata = { ...this.state.userdata };
    userdata[name] = value;
    this.setState({ userdata });
  };

  setIval = (value) => {
    const session = { ...this.state.session };
    session.ival = value;
    this.setState({ session });
  };

  render() {
    return (
      <Form
        setSession={this.setSession}
        updateState={this.updateState}
        state={this.state}
        setIval={this.setIval}
        insertData={this.insertData}
      />
    );
  }
}

export default App;
